import './bbutil'
import BBIFrame from './BBIFrame'

class BBPropertyPanel extends BBIFrame {
  getUrl = () => `/${window.bbFolderName}/w2ui/propertypanel.html`

  constructor() {
    super()
    this.scrolling = 'true'
    this.noEvents = false
    this.usesParameters=false; // do not use legacy way to interact with parameters
  }
}

export default BBPropertyPanel
